<template>
  <div id="ecommerce-wishlist-demo">
    <vs-row>
 <vs-col vs-type="flex" vs-w="8">
    <div class="items-grid-view vx-row match-height">
            <div class="vx-col lg:w-1/3 md:w-1/3 sm:w-1/2 w-full" v-for="item in products" :key="item.id">
                <item-grid-view :item="item">

                    <!-- SLOT: ACTION BUTTONS -->
                    <template slot="action-buttons">
                        <div class="flex flex-wrap">

                            <!-- SECONDARY BUTTON: MOVE TO CART -->
                            <div
                                class="item-view-secondary-action-btn bg-primary p-3 flex flex-grow items-center justify-center text-white cursor-pointer"
                                @click="cartButtonClicked(item)">
                                <feather-icon icon="ShoppingBagIcon" svgClasses="h-4 w-4" />

                                <span class="text-sm font-semibold ml-2" v-if="isInCart(item)">REMOVE FROM CART</span>
                                <span class="text-sm font-semibold ml-2" v-else>ADD TO CART</span>
                            </div>
                        </div>
                    </template>
                </item-grid-view>

            </div>
            <!-- RIGHT COL -->
                    
        </div>
  </vs-col>

   <vs-col vs-type="flex"  vs-w="4">
    <vx-card>
        <p class="text-grey mb-3">Cart</p>
        
        <div class="items-list-view" v-for="cartItem in cart" :key="cartItem.id">

            <item-list-view :item="cartItem">

                <!-- SLOT: ACTION BUTTONS -->
                <template slot="action-buttons">
                    <div
                        class="item-view-secondary-action-btn bg-primary p-3 rounded-lg flex flex-grow items-center justify-center text-white cursor-pointer"
                        @click="cartButtonClicked(cartItem)">
                        <feather-icon icon="ShoppingBagIcon" svgClasses="h-4 w-4" />
                    </div>
                </template>
            </item-list-view>

        </div>


        <vs-button class="w-full" @click="navigate()">CHECKOUT</vs-button>
    </vx-card>
  </vs-col>
        
    </vs-row>
  </div>
</template>

<script>
const ItemGridView = () => import('./components/ItemGridView.vue')
const ItemListView = () => import('./components/ItemListView.vue')

export default {
    mounted (){
        this.getProducts();
    },
    data () {
        return {
            products: [],
            cart: [],
            url: 'http://localhost:8000/'
        };
    },
    components: {
        ItemGridView,
        ItemListView
    },
    computed: {
        wishListitems() {
            return this.$store.state.eCommerce.wishList.slice().reverse();
        },
        isInCart() {
            return (item) => this.contains(item, this.cart);
        },
        isInWishList() {
            return (itemId) => this.$store.getters['eCommerce/isInWishList'](itemId)
        },
    },
    methods: {
        removeItemFromCart(item) {
            this.cart.splice(this.cart.indexOf(item),1);
        },
        cartButtonClicked(item) {
            if (this.contains(item, this.cart)) this.removeItemFromCart(item);
            else {
                this.additemInCart(item);
            }
        },
        additemInCart(item) {
            this.cart.push(item);
        },
        getProducts(){
            let fire = this;
            axios.get('http://api.task.test/api/products').then(function(response){
                fire.products = response.data;
                fire.cart.push(response.data[0]);
            }).catch(function(error){
                console.log(error);
            });
        },
        contains(obj, list) {
            var i;
            for (i = 0; i < list.length; i++) {
                if (list[i] === obj) {
                    return true;
                }
            }

            return false;
        },
        navigate() {
            this.$router.push({name: 'eCommerceCheckout', path:'/apps/eCommerce/checkout', params: this.cart});
        }
    }
}
</script>

